import { Box, Grid, Typography } from "@mui/material";
import React from "react";
import Home from "../pages/Home";
import "@fontsource/roboto/300.css";
import "@fontsource/roboto/400.css";
import "@fontsource/roboto/500.css";
import "@fontsource/roboto/700.css";
import { Branding } from "../config/Branding";
import { CircleLoader } from "react-spinners";

export default function MainLayout() {
  return (
    <>
      <Box bgcolor={Branding.two}>
        <Box maxWidth={Branding.maxWidth} marginLeft="auto" marginRight="auto">
          <Box py={2} mx={2}>
            <Grid container spacing={1} alignItems="center">
              <Grid item><CircleLoader size={28} color={Branding.three} speedMultiplier={0.25}/></Grid>
              <Grid item><Typography variant="h1" fontSize="1.4rem" color={Branding.three}>BAHRAIN SOFTWARE</Typography></Grid>
            </Grid>
            
          </Box>
        </Box>
      </Box>
      <Box maxWidth={Branding.maxWidth} marginLeft="auto" marginRight="auto">
        <Box py={2} mx={2}>
          <Home />
        </Box>
      </Box>
    </>
  );
}
