import { Box, Grid, Typography } from "@mui/material";
import React from "react";
import AnimatedText from "react-animated-text-content";
import { SquareLoader } from "react-spinners";
import { Branding } from "../config/Branding";

const align = "center";

export default function Home() {
  return (
    <Box textAlign={align}>
      <Head>Mission</Head>
      <T>
        Establish an elite taskforce that provides unprecedented Digital
        Transformation services for Bahrain and GCC.
      </T>
      <Head>Vision</Head>
      <T>
        Produce world-grade proprietary software designed for the GCC
        environment.
      </T>
      <Box>
        <Grid container spacing={2} alignItems="center" justifyContent="center">
          <Grid item>
            <SquareLoader size={16} color={Branding.one} />
          </Grid>
          <Grid item>
            <Typography color={Branding.one}>
              Under Construction. Please stay tuned.
            </Typography>
          </Grid>
        </Grid>
      </Box>
    </Box>
  );
}

function Head(props: { children?: any }) {
  return (
    <Typography
      variant="h2"
      textAlign={align}
      my={5}
      color={Branding.five}
      textTransform="uppercase"
    >
      <AnimatedText animationType="float" duration={0.3}>{props.children}</AnimatedText>
    </Typography>
  );
}

function T(props: { children?: any }) {
  return (
    <Typography
      fontSize="1.2rem"
      textAlign={align}
      mb={10}
      color={Branding.one}
    >
      <AnimatedText animationType="float">{props.children}</AnimatedText>
    </Typography>
  );
}
